.card-drink {
    flex: 1 1 35%;

    figure {
        padding: 5px;
        height: 100px;
        box-shadow: 2px 2px 5px rgb(63, 63, 63);
        overflow: hidden;
        text-align: center;
        cursor: pointer;
        transition: .1s;
        position: relative;
        margin: 0;
        flex: 1 1 35%;
        
        &:active {
            transform: translate(3px, 3px);
            box-shadow: 2px 2px 5px grey;
            transition: .1s;
        }
    }

    img{
        height: 70px;
    }

    figcaption {
        .price {
            position: absolute;
            top: 10px;
            right: 10px;
            background: #007bff;
            border-radius: 3px;
            padding: 8px;
            color: white;
        }
        .devise {
            font-size: smaller;
            margin-left: 2px;
        }
    }

    .card-quantity {
        display: flex;
        justify-content: center;
        margin-top: 10px;

        .card-number {
            font-weight: 700;
        }

        button {
            cursor: pointer;
            margin: 0 8px;
            // background: #35d6dc;
            color: white;
            height: 22px;
            width: 44px;
            border: none;
            font-weight: 700;
            box-shadow: 1px 1px 2px rgb(63, 63, 63);
            transition: .1s;
    
            &:active {
                box-shadow: -1px -1px 2px rgb(63, 63, 63);
                transition: .1s;
            }
        }
    }
}